body.show-modal .backdrop {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

body.show-modal .modal {
  transform: scale(1);
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity var(--transition-rule);
  background-color: rgba(46, 47, 66, .4);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.hi-backdrop {
  opacity: 1;
  pointer-events: none;
  visibility: hidden;
}

.backdrop.hi-backdrop .modal {
  transform: translate(-50%, -50%)scale(.9);
}

.modal {
  transition: transform var(--transition-rule);
  width: 579px;
  height: 501px;
  background-color: #fcfcfc;
  border-radius: 4px;
  flex-direction: column;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(.9);
  box-shadow: 0 2px rgba(0, 0, 0, .2), 0 1px rgba(0, 0, 0, .12), 0 1px rgba(0, 0, 0, .14);
}

.modal-btn {
  width: 28px;
  height: 28px;
  background-color: none;
  cursor: pointer;
  border: none;
  position: absolute;
  top: 31px;
  right: 31px;
}

.modal-btn:hover {
  background-color: none;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .8), 0 1px 1px rgba(46, 47, 66, .16), 1px 2px 1px rgba(46, 47, 66, .18);
}

.modal-icon {
